//Idea followed from :
//https://jasonwatmore.com/post/2020/07/09/angular-10-jwt-authentication-example-tutorial#authentication-service-ts
import { Injectable } from '@angular/core';
import { ApiService } from '../api.service';
import { BehaviorSubject, Observable, pipe } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { User } from 'src/app/models/user';
import { JWTTokenService } from '../jwttoken.service';
import { Router } from '@angular/router';
import { HttpParams } from '@angular/common/http';
import { min } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  private currentUserSubject!: BehaviorSubject<User>;
  public currentUser!: Observable<User>;

  constructor(private api: ApiService, private global: AppGlobalsVariable, private jwt: JWTTokenService, private router: Router) {
    this.currentUserSubject = new BehaviorSubject<User>(this.jwt.getUser());
    this.currentUser = this.currentUserSubject.asObservable();

  }

  public get currentUserValue() { return this.currentUserSubject.value; }
  public attemptAuth(user: User) {
    let path = this.global.User_Authentication;
    //AttemptAuth?Email=me.thapark@gmail.com&Password=password //Previously this url was used to get token info from server
    return this.api.post(path, user)
      .pipe
      (
        tap(res => {
          this.jwt.setToken(res.result.token);
          this.currentUserSubject.next(this.jwt.getUser());
          return res;
        })
      )
  }

  public register(user: User) {
    let path = this.global.User_Registration;
    return this.api.post(path, user)
      .pipe(
        tap(res => {
          return res.result;
        })
      );
  }

  public logoff() {
    this.jwt.purgeToken();
    this.currentUserSubject.next(new User());
    this.router.navigate(['/']);
  }

  public resetPassword(userid: string, oldPassword: string, currentPassword: string) {
    let path =  `${this.global.User_ResetPassword}/${userid}/${encodeURIComponent(currentPassword)}/${encodeURIComponent(oldPassword)}`;
    return this.api.get(path)
      .pipe(
        tap(res => {
          return res.result;
        })
      )
  }

  public sendForgotPasswordEmail(email: String) {
    let path = this.global.User_SendForgotPasswordEmail + email;
    return this.api.get(path)
      .pipe(
        tap(res => {
          return res.result;
        })
      )
  }

}
