import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterimages'
})
export class FilterimagesPipe implements PipeTransform {

  transform(items: any[], categoryId: number): any {
    if (categoryId === 1) {
      return items
    } else
      return items.filter(item => {
        return item.categoryId === categoryId;
      });
  }

}
