import { componentFactoryName } from '@angular/compiler';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminLayoutComponent } from './core/layout/admin-layout/admin-layout.component';
import { GuestLayoutComponent } from './core/layout/guest-layout/guest-layout.component';
import { InternalServerComponent } from './error-pages/internal-server/internal-server.component';
import { AboutUsComponent } from './modules/guest/about-us/about-us.component';
import { ContactUsComponent } from './modules/guest/contact-us/contact-us.component';
import { HomeComponent } from './modules/guest/home/home.component';
import { LoginComponent } from './modules/guest/login/login.component';
import { EmptyLayoutComponent } from './core/layout/empty-layout/empty-layout.component';
import { GalleryComponent } from './modules/guest/gallery/gallery.component';
import { WelcomeComponent } from './modules/guest/welcome/welcome.component';
import { PasswordResetComponent } from './modules/guest/password-reset/password-reset.component';
import { ForgotPasswordComponent } from './modules/guest/forgot-password/forgot-password.component';

const routes: Routes = [
  {
    path: '',
    component: GuestLayoutComponent,
    children: [
      { path: '', component: HomeComponent, pathMatch: 'full' },
      { path: 'contactus', component: ContactUsComponent, pathMatch: 'full' },
      { path: 'aboutus', component: AboutUsComponent, pathMatch: 'full' },
      { path: 'gallery', component: GalleryComponent, pathMatch: 'full' }
    ]
  },
  {
    path: '',
    component: EmptyLayoutComponent,
    children: [
      { path: 'login', component: LoginComponent, pathMatch: 'full' },
      { path: 'welcome', component: WelcomeComponent, pathMatch: 'full' },
      { path: "reset-password/:userid/:isForgot", component: PasswordResetComponent, pathMatch: 'full' },
      { path: 'forgot-password', component: ForgotPasswordComponent, pathMatch: 'full' }
    ]
  },
  {
    path: 'admin',
    component: AdminLayoutComponent,
    loadChildren: () => import('src/app/modules/admin/admin.module').then(m => m.AdminModule)
    // children: [
    //   { path: '', component: DashboardComponent, pathMatch: 'full' , canActivate:[AuthGuard]},
    //   { path: 'home', component: HomePageComponent, pathMatch: 'full', canActivate:[AuthGuard]},
    //   { path: 'aboutus', component: AboutUsComponent_Admin, pathMatch: 'full', canActivate:[AuthGuard] },
    //   { path: 'gallery', component: GalleryComponent_Admin, pathMatch: 'full', canActivate:[AuthGuard] },
    //   { path: 'contactus', component: ContactUsComponent_Admin, pathMatch: 'full', canActivate:[AuthGuard] },
    // ]
  },
  { path: '500', component: InternalServerComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
