import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { AppBootstrapModule } from './modules/app-bootstrap/app-bootstrap.module';
import { CoreModule } from './core/core.module';
import { InternalServerComponent } from './error-pages/internal-server/internal-server.component';
import { GuestModule } from './modules/guest/guest.module';
import { AdminModule } from './modules/admin/admin.module';
import { CustomerModule } from './modules/customer/customer.module';
import { AgmCoreModule } from '@agm/core';
import { AppGlobalsVariable } from './shared/app-global-variable';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ToastrModule } from 'ngx-toastr';


import { LoadingBarHttpClientModule } from '@ngx-loading-bar/http-client';// for HttpClient import:
import { LoadingBarModule, LOADING_BAR_CONFIG } from '@ngx-loading-bar/core'; // for Core import:
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpRequestInterceptor } from './core/interceptor/http-request.interceptor';
import { SplashScreenStateService } from './core/services/splash-screen-state.service';
import { SplashComponent } from './shared/splash/splash.component';
import { ImagePreviewComponent } from './shared/image-preview/image-preview.component';


@NgModule({
  declarations: [
    AppComponent,
    InternalServerComponent,
    SplashComponent,
    ImagePreviewComponent    
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    AppBootstrapModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDTD8hUxKzrjpnGCjSxIH41X3JxBroh97s',
      libraries: ['places']
    }),
    CoreModule,
    GuestModule,
    AdminModule,
    CustomerModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),

    LoadingBarHttpClientModule,
    LoadingBarModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: HttpRequestInterceptor, multi: true },
    { provide: LOADING_BAR_CONFIG, useValue: { latencyThreshold: 100 } },
    AppGlobalsVariable,
    SplashScreenStateService

  ],
  bootstrap: [AppComponent],

})
export class AppModule { }
