import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/models/user';
import { EnumSection } from 'src/app/shared/enums.enum';
import { AuthenticationService } from '../../services/authentication/authentication.service';
import { HomeService } from '../../services/home.service';

@Component({
  selector: 'app-admin-layout',
  templateUrl: './admin-layout.component.html',
  styleUrls: [
    './admin-layout.component.css',
]
})
export class AdminLayoutComponent implements OnInit {
  sidebarOpen = true;
  constructor(private homeService: HomeService, private auth: AuthenticationService) {
      this.auth.currentUser.subscribe(x=> this.currentUser = x);
   }

  currentUser!: User;
  ngOnInit(): void {
    this.homeService.getAllHomeContent()
    .subscribe(res=>{
      if(res.statusCode === 200)
      {
        if(res.result != null && res.result != undefined){
          localStorage.setItem("AllContent", JSON.stringify(res.result));
        }
      }
    });

  }
  sidebarToggler(){
    this.sidebarOpen = !this.sidebarOpen;
  }
}
