import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { SplashScreenStateService } from 'src/app/core/services/splash-screen-state.service';


///https://codemyui.com/circle-css3-animated-spinners/
@Component({
  selector: 'app-splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.css']
})
export class SplashComponent implements OnInit {

  constructor(private splashScreenStateService: SplashScreenStateService) {

  }
  displaySplash:boolean = true;

  ngOnInit(): void {
    // Somewhere the stop method has been invoked
    this.splashScreenStateService.subscribe(() => {
      this.hideSplashAnimation();
    });
  }

  private hideSplashAnimation() {
    this.displaySplash = false;
  }

}
