import { PlatformLocation } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SplashScreenStateService } from './core/services/splash-screen-state.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],

})
export class AppComponent implements OnInit {
  title = 'thewanderlustholidays';
  constructor(private router: Router, private splash: SplashScreenStateService, location: PlatformLocation) {
    location.onPopState(() => {
      this.stopLoaderSplash();
    });
  }

  ngOnInit(): void {
    console.clear();
    console.log("path: ", window.location.pathname);
    switch (window.location.pathname) {
      case "/":
        break;
      default:
        this.stopLoaderSplash();
        break;
    }
  }

  stopLoaderSplash() {
    setTimeout(() => {
      this.splash.stop();
    }, 1000);
  }

}
