import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent } from '../admin/dashboard/dashboard.component';
import { HomePageComponent } from '../admin/home-page/home-page.component';
import { AboutUsComponent_Admin } from '../admin/about-us/about-us.component';
import { ContactUsComponent_Admin } from '../admin/contact-us/contact-us.component';
import { GalleryComponent_Admin } from '../admin/gallery/gallery.component';
import { HappyCustomerAdminComponent } from '../admin/home-page/components/happy-customer-admin/happy-customer-admin.component';
import { AuthGuard } from '../../core/services/authentication/auth-guard';
import { ImagePreviewComponent } from 'src/app/shared/image-preview/image-preview.component';

const routes: Routes = [
  { path: '', component: DashboardComponent, pathMatch: 'full', canActivate: [AuthGuard] },
  { path: 'home', component: HomePageComponent, pathMatch: 'full', canActivate: [AuthGuard] },
  { path: 'aboutus', component: AboutUsComponent_Admin, pathMatch: 'full', canActivate: [AuthGuard] },
  { path: 'gallery', component: GalleryComponent_Admin, pathMatch: 'full', canActivate: [AuthGuard] },
  { path: 'contactus', component: ContactUsComponent_Admin, pathMatch: 'full', canActivate: [AuthGuard] },
  { path: 'image/:id', component: ImagePreviewComponent, pathMatch: 'full', canActivate: [AuthGuard] },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
