import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { HomeService } from 'src/app/core/services/home.service';
import { SplashScreenStateService } from 'src/app/core/services/splash-screen-state.service';
import { Content } from 'src/app/models/content';
import { EnumContentType, EnumSection, EnumSubSection } from 'src/app/shared/enums.enum';
import { environment } from 'src/environments/environment';

declare function init_custom_script(): any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {
  homeContents!: Content[];
  _WhatMakesDifferent!: Content[];
  packages: Content[] = [];
  _hotPackage!: Content;
  imageSrc: string = '';

  public get ImgBasePath(){ return environment.imageBasePath;}
  constructor(private homeService: HomeService, private toastr: ToastrService, private splashScreenStateService: SplashScreenStateService) { }
  ngOnInit(): void {
    // setTimeout(() => {
    //   //initialize the animation placed in the script files.
    //   init_custom_script();
    // }, 500);

    this.getContent();
  }



  getContent() {
    this.homeService.getAllHomeContent().subscribe(res => {
      if (res.statusCode === 200) {
        if (res.result !== null && res.result !== undefined) {
          let whatwe_offer = res.result.filter((x: any) => x.homeIdFK == EnumSection.What_We_Offer);
          localStorage.setItem("__whatwe_offer", JSON.stringify(whatwe_offer));
          let Data = res.result;
          this.homeContents = Data.filter((record: any) => record.homeIdFK === EnumSection.Root);
          if (this.homeContents != undefined) {
            this.imageSrc = this.homeContents.filter(x => x.graphicsURL != null)[0].graphicsURL;
          }

          this._WhatMakesDifferent = Data.filter((record: any) => record.homeIdFK === EnumSection.What_Makes_Us_Different);
          this.packages = Data.filter((record: any) => record.homeIdFK === EnumSection.Packages);
          this._hotPackage = Data.filter((x: any) => x.homeIdFK == EnumSection.Packages && x.contentType == EnumContentType[EnumContentType.HotPackage])[0];

          //bring the title, description to top but the features underneath the (title, description)
          var featureIntro = this._WhatMakesDifferent.filter((record) => record.subSectionName == EnumSubSection[EnumSubSection.What_Makes_Us_Different_Intro]);
          if (featureIntro != undefined || featureIntro != null) {
            this._WhatMakesDifferent.splice(this._WhatMakesDifferent.indexOf(featureIntro[0]), 1);
            this._WhatMakesDifferent.unshift(featureIntro[0]);
            let delayTime = 200;

            this._WhatMakesDifferent.forEach(feature => {
              delayTime += 100;
              feature.DelayAnimateTime = delayTime;
            });
          }
          setTimeout(() => {
            init_custom_script();
            this.splashScreenStateService.stop();
          }, 500)
        }
      }
    });
  }
}
