import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, RequiredValidator, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { GalleryService } from 'src/app/core/services/gallery.service';
import { environment } from 'src/environments/environment';
import { Category } from 'src/app/models/category';
import { CategoryService } from 'src/app/core/services/category.service';
@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent_Admin implements OnInit {

  constructor(private galleryService: GalleryService
    ,private toastr: ToastrService
    ,private categoryService: CategoryService) {}

  fromGallery: FormGroup = new FormGroup({
    'imageId': new FormControl(''),
    'categoryId': new FormControl('', [Validators.required]),
    'fileName': new FormControl('', [Validators.required]),
    'caption': new FormControl('')
  });

  file!: File;
  images!: any[];
  allImages: any[] = [];
  imageSrc: string = '';
  filterBy?: number = 1;
  categories: Category[] = []
  submitted: boolean = false;
  btnVisibility: boolean = false;
 selectedOption:any;
  public get f() { return this.fromGallery.controls; }
  public get galleryImagePath() { return environment.imageBasePath + "Gallery/" }
  ngOnInit(): void {
    this.filterBy = 1;
    this.galleryService.getImages()
    .subscribe((res) => {
      this.allImages = res.result
    });
    this.getAllCategories();
  }

  formGallerySubmit() {
    this.submitted = true;
    if (this.fromGallery.invalid) return;
  }

  onFileChange(event: any) {
    const reader = new FileReader();

    if (event.target.files && event.target.files.length) {
      this.file = event.target.files[0];
      reader.readAsDataURL(this.file);
      reader.onload = rs => {
        this.imageSrc = reader.result as string;

        const img = new Image();
        img.src = URL.createObjectURL(this.file);
        img.onload = (e: any) => {
          this.btnVisibility = true;
        };
      }
    }
  }

  onCategoryChange(e:any){
    this.fromGallery.patchValue({'categoryId': e.target.value});
  }


  uploadImage() {
    let categoryId = this.fromGallery.value.categoryId;
    this.galleryService.uploadImage(this.file, categoryId).subscribe(res => {
      if (res.statusCode === 200) {
        this.toastr.success("Image was successfully uploaded");
        this.btnVisibility = false;
      }
      else this.toastr.error("Oops! some error occured during upload. Please contact system provider");
    });
  }

  getAllCategories(){
    this.categoryService.getAllCategories()
    .subscribe(res=>{
      this.categories = res.result;
      console.log("this.categories: ", this.categories);
    })
  }

}
