import { Injectable } from '@angular/core';
import jwt_decode from 'jwt-decode';
import { User } from 'src/app/models/user';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { LocalStorageService } from './local-storage.service';

@Injectable({
  providedIn: 'root'
})
export class JWTTokenService {

  constructor(private global: AppGlobalsVariable, private ls: LocalStorageService) { }

  jwtToken: string = '';
  decodedToken: { [key: string]: string } = {};

  setToken(token: string) {
    if (token) {
      this.ls.setLocalStorage(this.global.token, token);
      this.jwtToken = token;
    }
  }

  purgeToken() {
    localStorage.removeItem(this.global.token);
  }

  public get token() {
    let _token = this.ls.getLocalStorage(this.global.token);
    if (_token !== "" && _token !== undefined) {
      //if expired delete token from local storage and then return null response
      if (this.isTokenExpired) {
        this.ls.purgeLocalStorage(this.global.token);
        return null;
      }
      return _token;
    }
    return null;
  }

  private decodeToken() {
    if (this.token) {
      this.decodedToken = jwt_decode(this.token);
    }
  }

  private getDecodeToken() {
    if (this.token != null)
      return jwt_decode(this.token);
    return null;
  }

  getUser() {
    if (this.token === null) return new User();

    this.decodeToken();
    let user: User = new User();
    user.email = this.getEmailId();
    user.id = this.getNameIdentifier();
    user.firstName = this.getFirstName();
    user.fullName = this.getFullName();
    user.mobilePhone = this.getMobilePhone();
    user.roles = this.getRoles();
    return user;
  }

  getEmailId() {
    if (this.token === null) return "";
    this.decodeToken();
    return this.decodedToken.email;
  }
  getNameIdentifier() {
    if (this.token === null) return "";
    this.decodeToken();
    return this.decodedToken.nameidentifier;
  }
  getFirstName() {
    if (this.token === null) return "";
    this.decodeToken();
    return this.decodedToken.FirstName;
  }
  getFullName() {
    if (this.token === null) return "";
    this.decodeToken();
    return this.decodedToken.FullName;
  }
  getMobilePhone() {
    if (this.token === null) return "";
    this.decodeToken();
    return this.decodedToken.mobilephoe;
  }
  getRoles() {
    if (this.token === null) return [];
    this.decodeToken();
    return this.decodedToken.Roles.split(',');
  }


  getExpiryTime() {
    let _token = localStorage.getItem(this.global.token);
    if (_token === null) return null;
    let _decodedToken: { [key: string]: string } = jwt_decode(_token);
    return _decodedToken ? _decodedToken.exp : null;
  }

  get isTokenExpired(): boolean {
    const expiryTime: number = parseInt(this.getExpiryTime() ?? "0");
    if (expiryTime > 0) {
      return ((1000 * expiryTime) - (new Date()).getTime()) < 5000;
    }
    return true;
  }

}
